body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Inter";
  font-weight: 600;
  font-size: 16px;
}
* {
  box-sizing: border-box;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 25px;
  background: #ffffff;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.23);
}

.nav--icon {
  height: 53px;
}

.nav--text {
  font-size: 30px;
  margin-left: 11px;
}
.nav--text a {
  color: rgba(31, 31, 31, 0.82);
}
.nav--text span {
  color: rgba(138, 0, 0, 0.82);
}

a {
  text-decoration: none;
}

nav {
  margin-left: auto;
}

nav li {
  list-style: none;
  display: inline-block;
}

.nav--links a {
  margin-right: 20px;
  font-size: 16px;
  font-style: normal;
  color: rgba(0, 0, 0, 0.6);
}

.download-button {
  /* position: absolute; */
  width: 150px;
  height: 100%;
  padding: 13px 15px;
  text-align: center;
  background: linear-gradient(270deg, #007560 39.48%, #ffffff 182.45%);
  box-shadow: inset 4px -4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 48.4615px;
  color: #ffffff;
}

.nav--main {
  display: flex;
  align-items: baseline;
}
